import {mapGetters,mapMutations,mapActions} from 'vuex'
import {playMode} from 'common/js/config'
import {shuffle} from 'common/js/util'

export const playlistMixin = {
  computed: {
    ...mapGetters([
      'playlist'
    ])
  },
  mounted() {//dom ready
    this.handlePlaylist(this.playlist)
  },
  activated() {//keep-aliveactivated
    this.handlePlaylist(this.playlist)

  },
  watch: {
    playlist(newVal) {
      this.handlePlaylist(newVal)
    }
  },
  methods: {
    handlePlaylist() {
      throw new Error('component must implement handlePlaylist method')
    }
  }
}

/* */
export const playerMixin = {
  computed: {
    iconMode(){//
      return this.mode === playMode.sequence? 'icon-sequence' : this.mode === playMode.loop ? 'icon-loop' :'icon-random'
    },
    ...mapGetters([//mode  sequenceList 
      'sequenceList',
      'currentSong',
      'playlist',
      'mode',
      'favoriteList'
    ])
  },
  methods: {
    changeMode(){//
      const mode = (this.mode +1) % 3

      this.setPlayMode(mode)//mapMutations 

      let list = null
      if(mode === playMode.random) {//
        list = shuffle(this.sequenceList)
      }else{//
        list = this.sequenceList
      }
      //
      this._resetCurrentIndex(list)

      /* */
      this.setPlaylist(list)
    },
    _resetCurrentIndex(list) {// currentSong  state.playlist[state.currentIndex] ||{} 
      let index = list.findIndex((item) => {//findIndex es6
        return item.id === this.currentSong.id
      })
      this.setCurrentIndex(index)
    },
    getFavoriteIcon(song){
      if(this.isFavorite(song)) {
        return 'icon-favorite'
      }
      return 'icon-not-favorite'

    },
    toggleFavorite(song){
      if(this.isFavorite(song)) {
        this.deleteFavoriteList(song)
      }else {
        this.saveFavoriteList(song)
      }
    },
    isFavorite(song){
      const index = this.favoriteList.findIndex((item) => {
          return song.id === item.id
      })
      return index > -1
    },
    ...mapMutations({
      setPlayingState:'SET_PLAYING_STATE',
      setCurrentIndex:'SET_CURRENT_INDEX',
      setPlayMode:'SET_PLAY_MODE',
      setPlaylist:'SET_PLAYLIST',
    }),
    ...mapActions([
      'saveFavoriteList',
      'deleteFavoriteList'
    ])
  }
}


export const searchMixin = {
  data(){
    return {
      query:'',
      refreshDelay:100
    }
  },
  computed: {
  ...mapGetters([
      'searchHistory'
    ])
  },
  methods: {
    /**/
    onQueryChange(query) {
      this.query = query
    },
    /* */
    blurInput(){
      this.$refs.searchBox.blur()
    },
    addQuery(item) {/*  */
      this.$refs.searchBox.setQuery(item)
    },
    /**/
    saveSearch(){
      this.saveSearchHistory(this.query)
    },
    ...mapActions([
      'saveSearchHistory',
      'deleteSearchHistory',
    ])
  }
}



